import React from 'react';
import img01 from'./img/01.png'
import img02 from'./img/02.png'
import img03 from'./img/03.png'
import img04 from'./img/04.png'
import img05 from'./img/05.png'
import img06 from'./img/06.png'

class Share extends React.Component{
    render(){
        return(
            <div>
                <h1>通过Github和自己的域名搭建网站</h1>
                <h2>1.前提条件</h2>
                <p>- 拥有自己的域名；</p>
                <p>- github仓库</p>
                <h2>2.在仓库添加CNAME文件并绑定自己的域名</h2>
                <img src={img01}></img>
                <p>文件只需填写域名</p>
                <img src={img02}></img>
                <p>进入仓库设置</p>
                <img src={img03}></img>
                <p>找到 Custom domain添加域名后保存即可。</p>
                <img src={img04}></img>
                <h2>3.添加域名解析</h2>
                <p>ping你的github.io域名，获取IP。</p>
                <img src={img05}></img>
                <p>在腾讯云域名解析页面进行记录</p>
                <p>添加两个A记录，用得到的IP，一个主机记录为：“www”，一个为“@”.</p>
                <img src={img06}></img>
            </div>
        )
    }
}

export default Share;
